import type { BaseRow, ColumnsType, Props } from '@/components/A-Crud'
import { ReCheckboxGroup, ReDeleteFlag, ReRadioGroup, ReSelect } from '@/components/A-Crud'
import { type KeysOf, type MapValueType, type ResultType, SystemParamsStore } from '@/stores'

interface Row extends BaseRow {
    /** id */
    id: number
    /**  */
    date: string
    /**  */
    name: string
    /**  */
    age: number
    /**  */
    sex: number
    /**  */
    hobby: string | string[]
    /**  */
    hobby2: string | string[]
    /**  */
    address: string
}

const columns: ColumnsType<Row> = {
    selection: {
        type: markRaw({}),
        label: '',
        tableProps: { type: 'selection', width: '50', selectable: row => row.id !== 5 },
    },
    index: {
        type: markRaw({}),
        label: '',
        tableProps: { type: 'index', width: '55', align: 'right' },
        render: ({ $index, crudOptions }) => {
            const { TableStore: { page: { currentPage, pageSize } } } = crudOptions
            const indexMethod = computed(() => (currentPage.value - 1) * pageSize.value + 1)
            return h('span', null, indexMethod.value + $index)
        },
    },
    id: {
        type: markRaw(ElInputNumber), // ()
        label: 'ID', // label

        // disabled: mode => mode === '',   // 
        // disabled: mode => mode === '',   // 
        // disabled: () => true,                // / 
        // visible: mode => mode === '',    // 
        // visible: mode => mode === '',    // 
        // searchDefaultValue: 1, // 
        // addDefaultValue: 2, // 
        formProps: {
            label: 'IDDDD', // 
            search: {}, //  
            edit: {}, //  /
        },
        componentProps: {
            min: 1,
            max: 100,
            search: {
                controlsPosition: 'right',
            },
        },
        tableProps: {
            minWidth: 90,
            align: 'center',
            // prop: 'sex',
            // sortable: true,
            resizable: false,
            // headerAlign: 'right',
            // className: 'bg-red-500! hover:bg-red-100!',
            // labelClassName: 'text-red-900 hover:text-red-500!',
        },
        render: ({ row }) => h(ElTag, null, () => `${row.id}`), // 
    },
    name: {
        type: markRaw(ElInput),
        label: '',
        formProps: {
            rules: [{ min: 3, max: 5, message: '35', trigger: 'blur' }],
            edit: {
                rules: [{ required: true, message: '', trigger: 'blur' }],
            },
        },
        componentProps: {
            // formatter: (value: string | number) => `$ ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ','),
            // parser: (value: string) => value.replace(/\$\s?|(,*)/g, ''),
            // showPassword: true,
            // suffixIcon: h(ElIcon, { }, { default: () => [h('i', { class: 'i-ep:clock' })] }),
            // TODO
            // slots: {
            //     prefix: () => h(ElIcon, { class: 'text-red-500' }, { default: () => [h('i', { class: 'i-ep:clock' })] }),
            //     suffix: () => h(ElIcon, { class: 'text-red-500' }, { default: () => [h('i', { class: 'i-ep:clock' })] }),
            // },
            maxlength: 20,
            showWordLimit: true,
        },
    },
    age: {
        type: markRaw(ElInputNumber),
        label: '',
        span: 12,
        componentProps: { showInput: true, min: 18, max: 70, step: 1 },
    },
    sex: {
        type: markRaw(ReRadioGroup),
        label: '',
        disabled: Mode => Mode === '',
        componentProps: { options: [], isButton: true },
    },
    hobby: {
        type: markRaw(ReSelect),
        label: '',
        componentProps: {
            options: [
                { value: 1, label: 'Option1' },
                { value: 2, label: 'Option2' },
                { value: 3, label: 'Option3' },
                { value: 4, label: 'Option4' },
                { value: 5, label: 'Option5' },
            ],
            placeholder: '',
            // multiple: true, // 
            filterable: true,
            // remote: true,
            // remoteMethod: (query: string) => {
            //     console.log(query)
            // },
            allowCreate: true,
        },
    },
    hobby2: {
        type: markRaw(ReCheckboxGroup),
        label: '',
        span: 12,
        componentProps: {
            options: [
                { value: 1, label: 'Option1' },
                { value: 2, label: 'Option2' },
                { value: 3, label: 'Option3' },
                { value: 4, label: 'Option4' },
                { value: 5, label: 'Option5' },
            ],
            min: 1,
            max: 3,
            search: { isButton: true },
        },
    },
    date: {
        type: markRaw(ElDatePicker),
        label: '',
        formProps: {
            label: 'xx',
            rules: [{ required: false, message: '', trigger: 'blur' }],
            edit: {
                required: true,
            },
        },
        componentProps: {
            type: 'month',
            // format: 'YYYY-MM',
            valueFormat: 'YYYY-MM',
            prefixIcon: h(ElIcon, {}, { default: () => [h('i', { class: 'i-ep:mic' })] }),
            edit: {
                type: 'daterange',
            },
        },
        // render: ({ row }) => h('span', null, `${row.date}+XX`),
    },
    address: {
        type: markRaw(ElInput),
        label: '',
    },
    deleteFlag: {
        type: markRaw(ReRadioGroup),
        label: '',
        searchDefaultValue: 0,
        componentProps: {
            options: [
                { value: 0, label: '' },
                { value: 1, label: '' },
            ],
            isButton: true,
        },
        tableProps: { width: 70, align: 'center' },
        render: ({ row }) => {
            return h(
                ReDeleteFlag,
                {
                    'size': 'small',
                    'modelValue': row.deleteFlag,
                    'onUpdate:modelValue': (newValue) => { row.deleteFlag = newValue },
                },
            )
        },
    },
    operatorName: {
        type: markRaw(ElInput),
        label: '',
    },
    createTime: {
        type: markRaw(ElInput),
        label: '',
    },
    updateTime: {
        type: markRaw(ElInput),
        label: '',
    },
}

const attribute: Props<Row> = {
    source: {
        fields: {
            filter: ['id', 'name', 'age', 'sex', 'hobby', 'hobby2', 'date', 'address', 'deleteFlag'],
            editor: ['id', 'name', 'age', 'sex', 'hobby', 'hobby2', 'date', 'address', 'deleteFlag'],
            column: ['id', 'name', 'age', 'sex', 'hobby', 'date', 'address', 'deleteFlag'],
            expand: Object.keys(columns) as (keyof Row)[],
        },
        columns,
    },
    apiConfig: {
        url: 'user',
        rowKey: 'userId',
    },
    attrs: {
        form: {
            // labelWidth: '120px',
        },
        drawer: {
            // closeOnClickModal: false,
        },
        table: {
            // height: 800,
            'onCell-contextmenu': (row: Row, column: any, cell: HTMLTableCellElement, event: Event) => {
                event.preventDefault()

                console.log(row, column, cell)
            },
        },
    },
    hooks: {
        beforeQueryHandler: ({ searchForm, formData }) => {
            // 
            if (Array.isArray(searchForm.hobby))
                formData.value.hobby = searchForm.hobby.join(',')
            if (Array.isArray(searchForm.hobby2))
                formData.value.hobby2 = searchForm.hobby2.join(',')

            return true
        },
    },
}

// TODO:  valuelabeldisabled 
// TODO
const ParamsMap = new Map<KeysOf<Row>, MapValueType>([
    ['deleteFlag', { key: 'delete_flag' }],
    // disabled1
    ['sex', { key: 'sex', value: item => item.itemId, label: item => item.itemValue, disabled: () => false }],
])
const Params: Partial<Record<KeysOf<Row>, ResultType[]>> = reactive({})
SystemParamsStore().GetSystemParams(ParamsMap).then((Res) => {
    Res.forEach((value, key) => {
        Params[key] = value
        if (columns[key]?.componentProps && columns[key].componentProps?.options)
            columns[key].componentProps.options = value
        else
            console.warn(`columns  ${key}  componentProps.options `)
    })
})

export { attribute, columns }
